.map-container-xx {
  height: 100%;
  width: 100%
}

.split,
.gutter.gutter-horizontal {
  float: left;
}

.gutter.gutter-vertical {
  float: left;
}

.gutter.gutter-horizontal {
  cursor: ew-resize;
  /* Must set gutter height when elements are floated. */
  height: 100%;
}

.gutter.gutter-vertical {
  cursor: eh-resize;
  /* Must set gutter height when elements are floated. */
  width: 100%;
}

#container {
  width: 100%;
  height: 100%;
}

/* Style the elements being split so they can be told apart. */
#red {
  background: #ff3c41;
  width: 100%;
}
#blue {
  background: #0ebeff;
  width: 100%;
}

